import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsInternalSplitHandle } from '@cds/core/internal-components/split-handle';

@Directive({ selector: 'cds-internal-split-handle' })
export class CdsInternalSplitHandleDirective {
  protected element: CdsInternalSplitHandle;

  get i18n() {
    return this.element.i18n;
  }
  @Input() set i18n(value) {
    this.element.i18n = value;
  };
  get direction() {
    return this.element.direction;
  }
  @Input() set direction(value) {
    this.element.direction = value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
